<template>
	<div>
		<ContentHeader3 title="Tindakan" url="/tindakan" title2="Data Tindakan" url2="/data-tindakan" subTitle="Tambah Data Tindakan" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Input Data Tindakan</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="prosesTindakan">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="kode_tindakan">Kode</label>
					                    <input type="text" class="form-control" id="kode_tindakan" name="kode_tindakan" disabled="" v-model="tindakan.kode_tindakan">
					                  </div>
					                  <div class="form-group">
					                    <label for="nama">Nama <span class="text-danger">*</span></label>
					                    <input type="text" class="form-control" id="nama" required="" name="nama" autocomplete="off" v-model="tindakan.nama">
					                  </div>
					                  <div class="form-group">
					                    <label for="jenis_tindakan">Jenis <span class="text-danger">*</span></label>
					                    <select class="custom-select form-control" v-model="tindakan.jenis_tindakan">
      										<option selected></option>
      										<option value="laser">Laser</option>
      										<option value="chemical peeling">Chemical Peeling</option>
      										<option value="thread">Thread</option>
      										<option value="konsultasi">Konsultasi</option>
      										<option value="injection">Injection</option>
      										<option value="surgery">Surgery</option>
      										<option value="serum treatment">Serum Treatment</option>
      										<option value="facial">Facial</option>
      										<option value="spa">Spa</option>
	    								</select>
					                  </div>
					                  <div class="form-group">
					                    <label for="lama_tindakan">Durasi <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Menit</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" required="" id="lama_tindakan" name="lama_tindakan"  v-model="tindakan.lama_tindakan">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="lama_tindakan">Tarif <span class="text-danger">*</span></label>
					                    <div class="row">
					                    	<div class="col">
					                    		<span>Umum</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="harga_pasien_baru" v-model="tindakan.harga_pasien_baru">
								                </div>
					                    	</div>
					                    	<div class="col">
					                    		<span>Member</span>
					                    		<div class="input-group mb-3">
								                  <div class="input-group-prepend">
								                    <div type="button" class="btn btn-secondary">Rp.</div>
								                  </div>
								                  <!-- /btn-group -->
					                    		  <input class="form-control" type="number" required="" name="harga_pasien_member" v-model="tindakan.harga_pasien_member">
								                </div>
					                    	</div>
					                    </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="modal">Modal <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="modal" required="" name="modal"  v-model="tindakan.modal">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Fee Dokter <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div class="btn btn-secondary">{{ nominalDokter }}</div>
						                    <a class="btn btn-secondary btn-flat dropdown-toggle dropdown-icon" data-toggle="dropdown">
						                      <span class="sr-only">Toggle Dropdown</span>
						                    </a>
						                    <div class="dropdown-menu" role="menu">
						                      <a class="dropdown-item" href="#" @click="changeNominalDokter()">Rupiah</a>
						                      <a class="dropdown-item" href="#" @click="changeNominalDokter()">Persen (%)</a>
						                    </div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="fee_dokter" required="" step="100" name="fee_dokter" v-model="tindakan.fee_dokter" @click="limitValueNominalDokter(tindakan.fee_dokter)" @keyup="limitValueNominalDokter(tindakan.fee_dokter)">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="point">Point</label>
					                    <input type="number" class="form-control" id="point" min="0" name="point"  v-model="tindakan.point">
					                  </div>
					                  <button type="submit" class="btn btn-primary d-flex align-items-center">
										  <div>
										  	Submit
										  </div>	
										  <div>
						                    <svg v-if="loading" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" style=" background: none; display: block; shape-rendering: auto;" width="200px" height="200px" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid">
						                      <g>
						                        <path d="M50 15A35 35 0 1 0 74.74873734152916 25.251262658470843" fill="none" stroke="#ffffff" stroke-width="12"></path>
						                        <path d="M49 3L49 27L61 15L49 3" fill="#ffffff"></path>
						                        <animateTransform attributeName="transform" type="rotate" repeatCount="indefinite" dur="1s" values="0 50 50;360 50 50" keyTimes="0;1"></animateTransform>
						                      </g>

						                    </svg>
						                  </div>
										</button>
					                </div>
					                <!-- /.card-body -->					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
import ContentHeader3 from '@/components/ContentHeader3'

import store from '@/store'
import router from '@/router'
import { reactive, computed, onMounted, ref } from 'vue'
import axios from 'axios'
import Swal from 'sweetalert2'
import tambah_nol from '@/tambah_nol.js'

export default{
	components: {
		ContentHeader3: ContentHeader3
	},
	setup(){
	    const { kode_tindakan_baru, generateKodeTindakan, kode_baru } = useKodeTindakan()

		const { tindakan, prosesTindakan, loading } = useProsesTindakan(kode_baru)


		const { 
			nominalDokter, 
			changeNominalDokter, 
			limitValueNominalDokter,
			nominalBeautician, 
			changeNominalBeautician, 
			limitValueNominalBeautician 
		} = useInputPersen(tindakan)

	    onMounted(() => {
	    	generateKodeTindakan()
	    })

	    return { 
	    	tindakan, 
	    	prosesTindakan, 
	    	kode_tindakan_baru, 
	    	generateKodeTindakan, 
	    	kode_baru,
			nominalDokter,
			changeNominalDokter,
			limitValueNominalDokter,
			nominalBeautician,
			changeNominalBeautician,
			limitValueNominalBeautician,
			loading
	    }
	}	
}	


function useKodeTindakan () {
	// Generate Kode Tindakan
	const user = computed(() => store.getters['auth/user'])
	const kode_tindakan_baru = reactive({})

    const generateKodeTindakan = () => {
    	axios.get(`api/cabang/getWhereId/${user.value.cabang_id}`)
    	.then((response) => {
    		kode_tindakan_baru.value = `${response.data.kode_cabang}-T`
    	}) 
    	.catch((error) => {
    		console.log(error)
    	})

    	axios.get('api/tindakan/getLastId')
    	.then((response) => {
    		if(response.data.id == null){
    			kode_tindakan_baru.value = `${kode_tindakan_baru.value}-${tambah_nol(1)}`
    		} else {
    			kode_tindakan_baru.value = `${kode_tindakan_baru.value}-${tambah_nol(response.data.id + 1)}`
    		}
    	})
    	.catch((error) => {
    		console.log(error)
    	})
    }

    const kode_baru = computed(() => kode_tindakan_baru.value)
    // End

    return {
    	kode_tindakan_baru,
    	generateKodeTindakan,
    	kode_baru
    }
}

function useProsesTindakan (kode_baru) {
	const loading = ref(false)
	const user = computed(() => store.getters['auth/user'])
	const tindakan = reactive({
  		cabang_id: user.value.cabang_id,
		kode_tindakan: kode_baru,
		jenis_tindakan: '',
		nama: '',
		lama_tindakan: '',
		harga_pasien_baru: '',
		harga_pasien_member: '',
		modal: '',
		fee_dokter: '',
		point: 0
	 })

	const resetDataTindakan = () => {
		tindakan.kode_tindakan = '',
		tindakan.jenis_tindakan = '',
		tindakan.nama = '',
		tindakan.lama_tindakan = '',
		tindakan.harga_pasien_baru = '',
		tindakan.harga_pasien_member = '',
		tindakan.modal = '',
		tindakan.fee_dokter = '',
		tindakan.point = 0
	}

	const prosesTindakan = () => {
		loading.value = true
		axios.post('api/tindakan/create', tindakan)
		.then((response) => {
			Swal.fire({
			  title: 'Berhasil!',
			  text: 'Berhasil menambahkan data',
			  icon: 'success',
			  confirmButtonText: 'Ok'
			})	
			loading.value = false
			console.log(response)
		})
		.catch((error) => {
			Swal.fire({
			  title: 'Gagal!',
			  text: 'Gagal menambahkan data',
			  icon: 'error',
			  confirmButtonText: 'Ok'
			})	
			loading.value = false
			console.log(error)
		})

		resetDataTindakan()
		router.replace('/data-tindakan')
    }

    return {
    	tindakan,
    	resetDataTindakan,
    	prosesTindakan,
    	loading
    }
}

function useInputPersen (tindakan) {
		const nominalDokter = ref('Rp.')

		const changeNominalDokter = () => {
			if (nominalDokter.value == 'Rp.') {
				nominalDokter.value = 'Persen (%)'
				tindakan.fee_dokter = 0
			} else if (nominalDokter.value == 'Persen (%)') {
				nominalDokter.value = 'Rp.'
				tindakan.fee_dokter = 0
			}
		}

		const limitValueNominalDokter = (value) => {
			if (nominalDokter.value == 'Rp.' && value < 0) {
				tindakan.fee_dokter = 0
			}

			if (nominalDokter.value == 'Persen (%)' && value > 100) {
				tindakan.fee_dokter = 100
			} else if (nominalDokter.value == 'Persen (%)' && value < 0) {
				tindakan.fee_dokter = 0
			}
		}


		return {
			nominalDokter,
			changeNominalDokter,
			limitValueNominalDokter
		}
	}

</script>


